function Sound() {
  this.AC = null;
  this.gainNode = null;
  this.source = null;
  this.count = 0;
  this.analyser = null;
  this.size = 256;
  this.ultimately = null;
  this.duration = null;
  this.init = () => {
    this.AC =
      new window.AudioContext() ||
      new window.webkitAudioContext() ||
      new AudioContext();
    console.log(this.AC.state);
    this.gainNode = this.AC[
      this.AC.createGain ? "createGain" : "creatGainNode"
    ]();
    this.gainNode.connect(this.AC.destination);
    this.analyser = this.AC.createAnalyser();
    this.analyser.fftSize = this.size * 2;
    this.analyser.connect(this.gainNode);
    this.ultimately = new Uint8Array(this.analyser.frequencyBinCount); // 获得用于可视化的数据流
  };
  this.frequencyAnalysis = null;
  // 设置音量 0 - 1
  this.setVolume = v => {
    this.gainNode.gain.value = v;
  };
  // 播放暂停
  this.playAndPause = isPlay => {
    isPlay ? this.AC.resume() : this.AC.suspend();
  };
  // 解析音频数据并播放
  this.analysisBufferPlay = (arraybuffer, callback) => {
    var n = ++this.count;
    this.source && this.source[this.source.stop ? "stop" : "noteOff"]();
    if (n !== this.count) return;
    this.AC.decodeAudioData(
      arraybuffer,
      buffer => {
        if (n != this.count) return;
        var bufferSource = this.AC.createBufferSource();
        bufferSource.buffer = buffer;
        bufferSource.connect(this.analyser);
        bufferSource[bufferSource.start ? "start" : "noteOn"](0);
        this.source = bufferSource;
        this.duration = bufferSource.buffer.duration; // s
        console.log(this);
        bufferSource.onended = () => {
          if (typeof callback === "function" && this.AC.state === "running") {
            callback();
          }
        };
        this.AC.resume();
      },
      function(err) {
        console.log(err);
      }
    );
  };

  // 返回实时数据
  this.visualizer = () => {
    // var requestAnimationFrame =
    //   window.requestAnimationFrame ||
    //   window.webkitRequestAnimationFrame ||
    //   window.mozRequestAnimationFrame;
    // var vn = () => {
    //   this.analyser.getByteFrequencyData(this.ultimately);
    //   callback(this.ultimately); // 向外部返回数据流
    //   requestAnimationFrame(vn);
    // };
    // this.frequencyAnalysis = requestAnimationFrame(vn);
    this.analyser.getByteFrequencyData(this.ultimately);
    return this.ultimately;
  };

  this.responseNowData = () => {};

  // 主动停止
  this.stop = () => {
    window.cancelAnimationFrame(this.frequencyAnalysis);
    this.source && this.source[this.source.stop ? "stop" : "noteOff"]();
  };
}
export { Sound };
