<template>
  <div class="starry-sky canvas-wrap">
    <canvas ref="sta" />
    <canvas ref="mov" />
    <canvas ref="met" />
  </div>
</template>
<script>
import { StaticStar, MoveStar, SmallMeteor } from "@/utils/anm-class";
// import { Clear } from "@/utils/tool";
// const clear = new Clear();
export default {
  data() {
    return {
      moverStarNum: 800,
      stticStarNum: 20,
      meteorNum: 30,
      staticStars: [],
      moveStars: [],
      meteor: [],
      width: 1920,
      height: 1080
    };
  },
  methods: {
    viewInit() {
      this.width = document.body.offsetWidth;
      this.height = document.body.offsetHeight;
      let sta = this.$refs.sta;
      let mov = this.$refs.mov;
      let met = this.$refs.met;
      sta.width = mov.width = met.width = this.width;
      sta.height = mov.height = met.height = this.height;
      let stactx = sta.getContext("2d");
      let movctx = mov.getContext("2d");
      let metctx = met.getContext("2d");
      this.createStaticStar(stactx);
      this.createMoveStar(movctx);
      this.createMeteor(metctx);
    },
    // 初始化静态的星星
    createStaticStar(ctx) {
      for (var i = 0; i < this.stticStarNum; i++) {
        let star = new StaticStar({
          width: this.width,
          height: this.height
        });
        star.draw(ctx);
        this.staticStars.push(star);
      }
    },
    // 初始化动态星星
    createMoveStar(ctx) {
      this.moveStars = [];
      for (var i = 0; i < this.moverStarNum; i++) {
        let star = new MoveStar({
          width: this.width,
          height: this.height
        });
        star.draw(ctx);
        this.moveStars.push(star);
      }
      const fun = (ctx, stars) => {
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
        stars.forEach(e => {
          e.draw(ctx);
        });
      };
      const params = [ctx, this.moveStars];
      const option = {
        fun,
        params
      };
      this.$emit("add", option);
    },
    // 初始化流星
    createMeteor(ctx) {
      this.meteor = [];
      for (let i = 0; i < this.meteorNum; i++) {
        let star = new SmallMeteor({
          width: this.width,
          height: this.height
        });
        star.draw(ctx);
        this.meteor.push(star);
      }
      const fun = (ctx, stars) => {
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
        stars.forEach(e => {
          e.draw(ctx);
        });
      };
      const params = [ctx, this.meteor];
      const option = {
        fun,
        params
      };
      this.$emit("add", option);
    }
  },
  mounted() {
    this.viewInit();
  }
};
</script>
<style lang="less" scoped></style>
